/* Estilos para la ruleta */
.roulette-wheel {
  width: 400px;
  height: 400px;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  margin: 0 auto;
  transition: transform 0.3s ease;
}

.roulette-marker {
  position: absolute;
  top: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-top: 30px solid #ff4d4d;
  z-index: 10;
}

.roulette-segment {
  position: absolute;
  width: 100%;
  height: 100%;
  clip-path: polygon(50% 50%, 50% 0, 100% 0);
  transform-origin: center;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 20px;
  box-sizing: border-box;
}

.segment-green {
  background: #4CAF50 !important;  /* Verde */
}

.segment-yellow {
  background: #FFD700 !important;  /* Amarillo */
}

.roulette-text {
  color: #333;
  text-shadow: 1px 1px 2px rgba(255, 255, 255, 0.7);
  transform-origin: center;
  pointer-events: none;
  font-size: 14px;
  font-weight: bold;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

.roulette-center {
  position: absolute;
  width: 50px;
  height: 50px;
  background: white;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  border: 3px solid #333;
}

/* Ajustes para el tema oscuro */
.dark-theme .roulette-segment.segment-green {
  background: #2E7D32 !important;  /* Verde más oscuro para tema oscuro */
}

.dark-theme .roulette-segment.segment-yellow {
  background: #FFC107 !important;  /* Amarillo más cálido para tema oscuro */
}

.dark-theme .roulette-text {
  color: #fff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7);
}

.dark-theme .roulette-center {
  background: #444;
  border-color: #666;
}
